export default {
	data() {
		return {
			socketTask:null,
			roomId:'100',
			msg:'',
			message:[],
			sendMsg:'',
			scrollTop:0
		};
	},
	methods: {
		sendMessage() {
			var me = this;
			var data = JSON.stringify({
				name:'TOM',
				msg:me.sendMsg,
				from:'me'
			})
			this.socketTask.send({
				// data: 'from ' + platform + ' : ' + parseInt(Math.random() * 10000).toString(),
				data: data,
				success(res) {
					console.log(res);
					me.message.push(JSON.parse(data));
					me.sendMsg='';
				},
				fail(err) {
					console.log(err);
				}
			})
		},
		lower: function(e) {
			console.log(e)
		},
	},
	onReady() {
		this.socketTask = uni.connectSocket({
			url: 'wss://connect.websocket.in/hello_uni_app?room_id=' + this.roomId,
			data() {
				return {
					msg: 'Hello'
				}
			},

			header: {
				'content-type': 'application/json'
			},


			method: 'GET',

			success(res) {
				console.log("success:"+res)
				// 这里是接口调用成功的回调，不是连接成功的回调，请注意
			},
			fail(err) {
				// 这里是接口调用失败的回调，不是连接失败的回调，请注意
			}
		})
		this.socketTask.onOpen((res) => {
			this.connecting = false
			this.connected = true
			uni.hideLoading()
			uni.showToast({
				icon: 'none',
				title: '连接成功'
			})
			console.log('onOpen', res);
		})
		this.socketTask.onError((err) => {
			this.connecting = false
			this.connected = false
			uni.hideLoading()
			uni.showModal({
				content: '连接失败，可能是websocket服务不可用，请稍后再试',
				showCancel: false
			})
			this.connect();
			console.log('onError', err);
		})
		this.socketTask.onMessage((res) => {
			this.msg = JSON.parse(res.data);
			this.msg.from = 'you';
			this.message.push(this.msg);
			this.scrollTop=(this.message.length+1)*50;
			console.log('onMessage', res)
		})
		this.socketTask.onClose((res) => {
			this.connected = false
			this.startRecive = false
			this.socketTask = false
			this.msg = false
			console.log('onClose', res)
		})
	}
}
